body {
  color: #f0f8ff;
  margin: 0;
  font-family: Lucida Sans, Lucida Sans Regular, Lucida Grande, Lucida Sans Unicode, Geneva, Verdana, sans-serif;
  overflow: hidden;
}

.scores {
  color: #f0f8ff;
  align-items: center;
  font-size: 24px;
  display: flex;
  position: absolute;
  top: 0;
  left: 50%;
}

canvas {
  background: #000;
}

.screen {
  z-index: 100;
  background: #000;
  position: absolute;
  inset: 0;
}

.screen.hidden {
  display: none;
}

.screen h2, .screen p {
  text-align: center;
  font-size: 50px;
}

.screen p {
  font-size: 15px;
}

.screen a {
  color: #0ff;
}

.start-game-button {
  cursor: pointer;
  text-transform: uppercase;
  background-color: beige;
  width: 190px;
  height: 100px;
  font-size: 30px;
  font-weight: 900;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
